import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';
import { PatientMapper } from '../../../models/patient.model';
import { EvolutionsProvider } from '../../../providers/evolutions/evolutions';
import { EvolutionMapper } from '../../../models/evolution.model';
import { PatientEvolutionDetailPage } from '../../index';

/**
 * Generated class for the PatientEvolutionsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'PatientEvolutionsPage'
})
@Component({
  selector: 'page-patient-evolutions',
  templateUrl: 'patient-evolutions.html',
})
export class PatientEvolutionsPage {

  public patient: PatientMapper;

  public searching: boolean = false;

  public currentItems: any[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,
              private evolutions: EvolutionsProvider, private modalCtrl: ModalController) {
    this.patient = this.navParams.get('patient');
    this.searching = true;
    this.evolutions.patientEvolutions(this.patient.patient_id ? this.patient.patient_id : this.patient.id).subscribe((data: EvolutionMapper[]) => {
      this.searching = false;
      this.currentItems = data;
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PatientEvolutionsPage');
  }

  openItem(item: EvolutionMapper) {
    let evolutionDetailModal = this.modalCtrl.create(PatientEvolutionDetailPage, { evolution: item });
    evolutionDetailModal.present();
  }

}



